.nav.active {
  max-height: 500px;
  transition: max-height .3s; }

.nav--tablet-up {
  display: none; }

@media only screen and (min-width: 48rem) {
  .nav--mobile {
    display: none; }
  .nav--tablet-up {
    display: block;
    height: 120px; }
    .nav--tablet-up .nav__inner {
      display: flex;
