      align-items: center;
      height: 116px; }
      .nav--tablet-up .nav__inner .nav__logo a {
        height: 54px;
        width: 95px;
        background-image: url(../img/logo.png);
        display: block;
        background-size: cover;
        background-repeat: no-repeat; }
      .nav--tablet-up .nav__inner .nav__links {
        width: 580px;
        display: flex;
        justify-content: space-between; }
        .nav--tablet-up .nav__inner .nav__links li {
          padding: 0 6px;
          display: flex;
          align-items: center;
          position: relative; }
          .nav--tablet-up .nav__inner .nav__links li a {
            font-family: "Titillium Web", "Montserrat", sans-serif;
            text-decoration: none;
            color: #fff;
            font-size: 12px;
            text-align: center; }
          .nav--tablet-up .nav__inner .nav__links li a:hover {
            color: #c7ae69;
